import React from "react";
import ReactDOM from 'react-dom';
import './Home.css';
import 'bootstrap/dist/css/bootstrap.min.css';

class Home extends React.Component {
  
  constructor(props){
    super(props);
    this._mounted = false;
    this.state = {
      itemsArray: [],
      loading: false,
      prevY: 0,
      tagInputValue: null,
      tags: [],
      showTags: false
    };
    this.handleObserver = this.handleObserver.bind(this);
    this.fetchMoreData = this.fetchMoreData.bind(this);
    this.handleTopicChange = this.handleTopicChange.bind(this);
    this.handleTopicSubmit = this.handleTopicSubmit.bind(this);
    this.handleTopicReset = this.handleTopicReset.bind(this);
    this.getCommonOutlets = this.getCommonOutlets.bind(this);
  }

  handleObserver(entities, observer) {
    const y = entities[0].boundingClientRect.y;
    if (this.state.prevY > y || (this.state.itemsArray.length < 10 && this.state.loading === false)) {
      this.fetchMoreData();
    }
    if(this._mounted){
      this.setState({ 
        prevY: y 
      });
    }
  }

  handleTopicChange(event){
    event.preventDefault();
    if(this._mounted){
      this.setState({
        tagInputValue: event.target.value
      });
    }
  }

  handleTopicSubmit(event){
    event.preventDefault();
    let newTags = this.state.tags;
    newTags.push(" " + this.state.tagInputValue);
    this.setState({
    	tags: newTags,
    	tagInputValue: null,
    	showTags: true
    })
    document.getElementById("topicForm").reset(); 
  }

  handleTopicReset(event){
    event.preventDefault();
    document.getElementById("topicForm").reset(); 
    if(this._mounted){
      this.setState({
        tagInputValue: null,
        tags: [],
        showTags: false
      });
    }
  }

  getCommonOutlets(outlet){
    switch(outlet){
      case 'www.nytimes.com':
        return 'New York Times';
      case 'huffpost.com':
        return 'Huffington Post';
      case 'economist.com':
        return 'The Economist';
      case 'politico.com':
        return 'Politico';
      case 'cnn.com':
        return 'CNN';
      case 'news.yahoo.com':
        return 'Yahoo News';
      case 'news.google.com':
        return 'Google News';
      case 'foxnews.com':
        return 'Fox News';
      case 'nbcnews.com':
        return 'NBC';
      case 'washingtonpost.com':
        return 'The Washington Post';
      case 'theguardian.com':
        return 'The Guardian';
      case 'the Guardian':
      	return 'The Guardian';
      case 'wsj.com':
        return 'The Wall Street Journal';
      case 'abcnews.go.com':
        return 'ABC News';
      case 'bbc.co.uk':
        return 'BBC';
      case 'usatoday.com':
        return 'USA Today';
      case 'latimes.com':
        return 'LA Times';
      case 'medium.com':
        return 'Medium';
      default:
        return outlet;
    }
  }

  fetchMoreData(){
    this.setState({ loading: true });
    fetch('https://goodnewsbucket.s3.us-east-2.amazonaws.com/data/articles.json', {
      method: 'get',
      headers: {'Content-Type': 'application/json'},
    })
      .then(res => res.text())
      .then(res => JSON.parse(res))
      .then(res => {
        let itemsArray = this.state.itemsArray;
        let i = itemsArray.length;
        let newItem = {
          title: res[i].title,
          description: res[i].description,
          outlet: this.getCommonOutlets(res[i].publisher),
          date: res[i].publish_date,
          thumbnailURL: res[i].thumbnail,
          articleURL: res[i].url,
          sentimentrank: res[i].sentimentrank,
          tags: res[i].tags
        }
        itemsArray.push(newItem);
        if(this._mounted){
          this.setState({
            itemsArray: itemsArray,
            loading: false
          });
        }
      })
      .catch(err => err)
  }

  render() {

    let newsItems = [];
    for (let i = 0; i < this.state.itemsArray.length; i++) {
      var matchesTags = false
      if (this.state.showTags){
      	for (var j in this.state.tags){
      		if(this.state.itemsArray[i].tags.indexOf(this.state.tags[j].trim()) > -1){
      		    matchesTags = true;
      		}
      	}
      }
      if (matchesTags || !this.state.showTags){
      		let allTags = [];
      	  	for (var k in this.state.itemsArray[i].tags){
      	  		allTags.push(" " + this.state.itemsArray[i].tags[k]);
      	  	}
      	  	if (allTags === null){
      	  		allTags = [' none']
      	  	}
	      newsItems.push(
	        <div className="newsItemCSS" key={i}> 
	            <img 
	              src={this.state.itemsArray[i].thumbnailURL}
	              height='185' 
	              width='185'
	              align='left'
	              alt='thumbnail not found'>
	            </img> 
	          	<br />  
	          	<h3 className="newsTextCSS"> <a href={this.state.itemsArray[i].articleURL} target="_blank" rel="noopener noreferrer">{this.state.itemsArray[i].title}</a> | {this.state.itemsArray[i].outlet} </h3>
	          <p><i>{this.state.itemsArray[i].date} </i>|<i> Sentiment Rank: {this.state.itemsArray[i].sentimentrank}</i></p>
	          <p className="newsTextCSS">{this.state.itemsArray[i].description}</p>
	          <p className="newsTextCSS">Tags:{allTags.toString()}</p>
	        </div>
	      );
	   }
    }

    return (
      <div>
        <div className="headerCSS">
          <h1>News Feed</h1>
        </div>
        <div className="topicFormCSS">
          <form onSubmit={this.handleTopicSubmit} id="topicForm">
             <label>
                Topic:
                <input 
                  className="topicFormInputCSS" 
                  type="text"
                  value={this.state.tagInputValue}
                  onChange={this.handleTopicChange}
                />
             </label>
             <input type="submit"
               value="Filter"
             />
             <button onClick={this.handleTopicReset}>Reset</button>
          </form>
          {this.state.showTags ? <p>Tags:{this.state.tags.toString()}</p> : null}
        </div>

        {newsItems}

        <div className="loadingCSS" ref={loadingRef => (this.loadingRef = loadingRef)}>
          {(this.state.loading || this.state.itemsArray.length <= 5) ? <div align='center'>Loading...</div> : null}
        </div>
      </div>
    );
  }

  componentDidMount(){

    this._mounted = true;

    for (var i = 0; i < 10; i++) {
        this.fetchMoreData();
    }

    var options = {
      root: null,
      rootMargin: "0px",
      threshold: 1.0
    };
    
    this.observer = new IntersectionObserver(this.handleObserver, options);
    this.observer.observe(this.loadingRef);
  }

  componentWillUnmount(){
    this._mounted = false;
  }
}

// ========================================

ReactDOM.render(
  <Home />,
  document.getElementById('root')
);

export default Home;